import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { BarChart3, Users, Share2, Eye, TrendingUp, Calendar } from "lucide-react";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { subDays, startOfDay } from "date-fns";

type TimeRange = "today" | "7days" | "30days" | "all";

const AdminActivityStatsList = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { getLocalizedField } = useLocalizedContent();
  const [timeRange, setTimeRange] = useState<TimeRange>("30days");

  const getDateFilter = () => {
    const now = new Date();
    switch (timeRange) {
      case "today":
        return startOfDay(now).toISOString();
      case "7days":
        return startOfDay(subDays(now, 6)).toISOString();
      case "30days":
        return startOfDay(subDays(now, 29)).toISOString();
      default:
        return null;
    }
  };

  // Fetch all published activities (exclude drafts)
  const { data: activities, isLoading: activitiesLoading } = useQuery({
    queryKey: ['admin-activities-stats-list'],
    queryFn: async () => {
      const { data, error } = await supabase
        .from('activities')
        .select('*')
        .neq('status', 'draft')
        .order('created_at', { ascending: false });
      if (error) throw error;
      return data;
    },
  });

  const dateFilter = getDateFilter();

  // Fetch all subscriptions count per activity
  const { data: subscriptionCounts } = useQuery({
    queryKey: ['admin-all-subscription-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('subscriptions').select('activity_id, created_at');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(sub => {
        counts[sub.activity_id] = (counts[sub.activity_id] || 0) + 1;
      });
      return counts;
    },
  });

  // Fetch all shares count per activity
  const { data: shareCounts } = useQuery({
    queryKey: ['admin-all-share-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('activity_shares').select('activity_id, created_at');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(share => {
        counts[share.activity_id] = (counts[share.activity_id] || 0) + 1;
      });
      return counts;
    },
  });

  // Fetch all page views count per activity
  const { data: viewCounts } = useQuery({
    queryKey: ['admin-all-activity-view-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('page_views').select('target_id, created_at').eq('page_type', 'activity');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(view => {
        if (view.target_id) {
          counts[view.target_id] = (counts[view.target_id] || 0) + 1;
        }
      });
      return counts;
    },
  });

  // Fetch participating restaurants count per activity
  const { data: restaurantCounts } = useQuery({
    queryKey: ['admin-all-activity-restaurant-counts'],
    queryFn: async () => {
      const { data, error } = await supabase
        .from('activity_restaurants')
        .select('activity_id');
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(ar => {
        counts[ar.activity_id] = (counts[ar.activity_id] || 0) + 1;
      });
      return counts;
    },
  });

  // Calculate total stats
  const totalViews = viewCounts ? Object.values(viewCounts).reduce((a, b) => a + b, 0) : 0;
  const totalSubscribers = subscriptionCounts ? Object.values(subscriptionCounts).reduce((a, b) => a + b, 0) : 0;
  const totalShares = shareCounts ? Object.values(shareCounts).reduce((a, b) => a + b, 0) : 0;
  const totalRestaurants = restaurantCounts ? Object.values(restaurantCounts).reduce((a, b) => a + b, 0) : 0;

  if (activitiesLoading) {
    return (
      <div className="space-y-6">
        <Skeleton className="h-8 w-48" />
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {[1, 2, 3, 4].map(i => <Skeleton key={i} className="h-24" />)}
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {[1, 2, 3, 4, 5, 6].map(i => <Skeleton key={i} className="h-40" />)}
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div className="flex items-center gap-2">
          <TrendingUp className="h-6 w-6 text-primary" />
          <h1 className="text-2xl font-bold">{t('admin.activityStatistics')}</h1>
        </div>
        <div className="flex items-center gap-2">
          <Calendar className="h-4 w-4 text-muted-foreground" />
          <Select value={timeRange} onValueChange={(v) => setTimeRange(v as TimeRange)}>
            <SelectTrigger className="w-[140px]">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="today">{t('analytics.today', '今日')}</SelectItem>
              <SelectItem value="7days">{t('analytics.last7Days', '近7天')}</SelectItem>
              <SelectItem value="30days">{t('analytics.last30Days', '近30天')}</SelectItem>
              <SelectItem value="all">{t('analytics.allTime', '所有时间')}</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Overview Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-primary/10 rounded-lg">
                <Eye className="h-5 w-5 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalViews')}</p>
                <p className="text-2xl font-bold">{totalViews}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-red-500/10 rounded-lg">
                <Users className="h-5 w-5 text-red-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalSubscribers')}</p>
                <p className="text-2xl font-bold">{totalSubscribers}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-green-500/10 rounded-lg">
                <Share2 className="h-5 w-5 text-green-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalShares')}</p>
                <p className="text-2xl font-bold">{totalShares}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-amber-500/10 rounded-lg">
                <BarChart3 className="h-5 w-5 text-amber-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.participatingRestaurants')}</p>
                <p className="text-2xl font-bold">{totalRestaurants}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Activity List */}
      <div>
        <h2 className="text-lg font-semibold mb-4">{t('admin.allActivities')}</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {activities?.map(activity => {
            const title = getLocalizedField(activity, 'title');
            const views = viewCounts?.[activity.id] || 0;
            const subscribers = subscriptionCounts?.[activity.id] || 0;
            const shares = shareCounts?.[activity.id] || 0;
            const restaurants = restaurantCounts?.[activity.id] || 0;

            return (
              <Card 
                key={activity.id} 
                className="cursor-pointer hover:shadow-md transition-shadow"
                onClick={() => navigate(`/admin/activity-stats/${activity.id}`)}
              >
                <CardHeader className="pb-2">
                  <CardTitle className="text-base line-clamp-2">{title}</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-2 gap-3 text-sm">
                    <div className="flex items-center gap-2">
                      <Eye className="h-4 w-4 text-muted-foreground" />
                      <span>{views} {t('admin.views')}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Users className="h-4 w-4 text-muted-foreground" />
                      <span>{subscribers} {t('admin.subscribers')}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Share2 className="h-4 w-4 text-muted-foreground" />
                      <span>{shares} {t('admin.shares')}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <BarChart3 className="h-4 w-4 text-muted-foreground" />
                      <span>{restaurants} {t('admin.restaurants')}</span>
                    </div>
                  </div>
                  <Button variant="outline" size="sm" className="w-full mt-4">
                    {t('admin.viewDetails')}
                  </Button>
                </CardContent>
              </Card>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default AdminActivityStatsList;
